import { Component, Prop, h } from '@stencil/core';
import { background, color } from 'storybook/internal/theming';


@Component(
{
    tag: 'my-list',
    styleUrl: 'my-list.css',
    shadow: true,
})



export class MyList
{
    @Prop() items: string[] =['Item 1', 'Item 2', 'Item 3'];
    @Prop() spaceBetweenItems: number = 10;
    @Prop() direction: 'column' | 'row' = 'row';
    @Prop() wrap: boolean = false;
    render()
    {
        const classes ={
            'ListObject': true,
        }

        const styleUl = {
            display: "flex",
            gap: `${this.spaceBetweenItems * 0.25}rem`, // gap works on the flex container
            flexWrap: this.wrap ? "wrap" : "nowrap",
            flexDirection: this.direction,
            listStyle: 'none', // Good practice to remove default ul list styling
            padding: '0',     // Remove default padding
            margin: '0',      // Remove default margin
            background:"--theme-select-list--background",
        }
        const needsScroll = this.direction === 'row' && !this.wrap;
        return(
          <div>
            <ix-style-loader></ix-style-loader>
            <div style={needsScroll ? { overflowX: 'auto', width: '100%' } : {}}>
                <ul style={styleUl}>
                {this.items.map(item => (
                    <li class={classes}>{item}</li>
                ))}
                </ul>
            </div>
          </div>

        )
    }
}
