:host {
    display: block;
    width: 100%;
}

.ListObject
{
  border: 2px solid var(--theme-select-list--border-color) ;
  border-radius: 10px;
  box-shadow: 5px 5px 5px var( --theme-menu--box-shadow);
  overflow: hidden;
  text-align: center;
  max-width: 250px;

}

.ListObject{
    background-color: var(--theme-select-list-item--background);
    color: var(--theme-select-list-item--color);
}

.ListObject:hover{
    background-color: var(--theme-select-list-item--background--hover);
    color: var(--theme-select-list-item--color--hover);
    cursor: pointer;
}

::slotted(li) {
  min-width: 120px;
}

ul {
  width: 100%;
  flex-wrap: wrap; /* Allow wrapping always */
}

/* On mobile: stack vertically */
@media (max-width: 768px) {
  ul {
    flex-direction: column !important;
    gap: 0.5rem;
  }
}
